<template>
  <div class="hello">
    <img v-if="currentImage" :src="currentImage.src" class="questionImage"><br/>
    <input v-if="currentImage" v-model="answer" @input="onAnswer" ref="answerRef" @blur="$refs.answerRef.focus()"><br/>
    <div style="padding-top:10px;" v-if="currentImage">hold ctrl for answers</div>
    <div v-if="showHint" style="padding-top:20px;">{{currentImage.label.join(', ')}}</div>
  </div>
</template>

<script>

import { vowImage } from './vowImage';

export default {
  name: 'HelloWorld',
  components: vowImage,
  data() {
    return {
      images: [
        { src: 'arrival.jpg', label: ['прибытие','arrival'] },
        { src: 'love.jpg', label: ['любовь','цветок','love'] },
        { src: 'black_garden.jpg', label: ['сад','черный сад','чёрный сад','black garden', 'garden'] },
        { src: 'black_heart.jpg', label: ['чёрное сердце','черное сердце','клякса','black heart', 'blot'] },
        { src: 'dread.jpg', label: ['дырка','скорбь','dread'] },
        { src: 'darkness.jpg', label: ['тьма','darkness'] },
        { src: 'drink.jpg', label: ['чаша','напиток','drink'] },
        { src: 'earth.jpg', label: ['земля','earth'] },
        { src: 'entrance.jpg', label: ['капля','вход','entrance', 'enter'] },
        { src: 'fleet.jpg', label: ['пирамиды','флот','fleet'] },
        { src: 'forsaken.jpg', label: ['форсейкен','презренные','forsaken'] },
        { src: 'gift.jpg', label: ['руки','передача','gift', 'hands'] },
        { src: 'guardian.jpg', label: ['гардиан','страж','guardian'] },
        { src: 'higher_dimension.jpg', label: ['казан','высшее измерение','higher dimension'] },
        { src: 'hive.jpg', label: ['глаза','улей','hive'] },
        { src: 'light.jpg', label: ['свет','light'] },
        { src: 'love.jpg', label: ['цветок','любовь','love', 'flower'] },
        { src: 'memory.jpg', label: ['мозг','память','memory', 'brain'] },
        { src: 'murder.jpg', label: ['япония','убийство','murder', 'kill'] },
        { src: 'pyramid.jpg', label: ['треугольник','пирамида','pyramid'] },
        { src: 'savathun.jpg', label: ['вичквин','саватун','savathun'] },
        { src: 'stop.jpg', label: ['квадрат','стоп','stop'] },
        { src: 'tower.jpg', label: ['баннер','башня','tower'] },
        { src: 'traveler.jpg', label: ['странник','traveler'] },
        { src: 'witness.jpg', label: ['витнесс','свидетель','witness'] },
        { src: 'worm.jpg', label: ['змея','червь','worm'] },
        { src: 'worship.jpg', label: ['бог','поклонение','worship'] },
      ],
      currentImage: { src: 'arrival.jpg', label: 'прибытие' },
      questionHistory: [],
      currentIndex: 0,
      answer: '',
      showHint: false,
    }
  },
  methods: {
    onAnswer() {
      if (this.currentImage.label.indexOf(this.answer) > -1) {
        !this.currentQuestionIsInHistory() ? this.addCurrentQuestionToHistory() : this.removeCurrentQuestionFromHistory();
        this.setNewQuestion();
      }
    },
    currentQuestionIsInHistory() {
      return this.questionHistory.indexOf(this.currentIndex) > -1;
    },
    addCurrentQuestionToHistory() {
      this.questionHistory.push(this.currentIndex);
    },
    removeCurrentQuestionFromHistory() {
      this.questionHistory = this.questionHistory.filter(e => e !== this.currentIndex);
    },
    setNewQuestion() {
        this.answer = '';
        var newImageIndex = this.getNewRandomQuestionIndex();
        this.currentImage = this.images[newImageIndex];
        this.currentIndex = newImageIndex;      
    },
    getNewRandomQuestionIndex() {
      var res = Math.floor(Math.random() * this.images.length);
      while (res === this.currentIndex || this.questionHistory.indexOf(res) > -1) {
        res = Math.floor(Math.random() * this.images.length);
      }
      return res;
    },
    toggleHint(val) {
      this.showHint = val !== null ? val : !this.showHint;
    },
  },
  mounted() {
    this.setNewQuestion();
    window.addEventListener('keydown', (e) => {
      if (e.key == 'Control') {
        this.toggleHint(true);
      }
    });
    window.addEventListener('keyup', (e) => {
      if (e.key == 'Control') {
        this.toggleHint(false);
      }
    });
  },
  props: {
    msg: String
  }
}
</script>

